.footer {
  position: relative;
  z-index: 10;
  padding: 42px 0px;
  background: linear-gradient(180deg, #FFD600 0%, #FFC700 100%);
  

  &__inner {
    display: grid;
    grid-template-columns: min-content min-content min-content;
    justify-content: space-between;
  }

  &__logo {}

  &__logo > div {
    display: none;
  }

  &__links {
    display: grid;
    gap: 42px;
    grid-auto-flow: column;
    align-items: center;
  }

  &__link {
    white-space: nowrap;
    padding: 4px 16px;
    border-radius: 8px;
    font-weight: bold;
    font-size: 20px;
    line-height: 36px;
    color: rgba(0, 0, 0, 0.7);
    transition: all .3s ease;
    &:hover {
      background: rgba(0, 0, 0, 0.1);
      color: rgba(0, 0, 0, 1);
    }
  }

  &__socials  {
    display: grid;
    gap: 12px;
    grid-auto-flow: column;
    align-items: center;
  }

  &__social {
    width: 52px;
    height: 52px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    transition: all .3s ease;
    &:hover {
      background: rgba(0, 0, 0, 0.1)
    }
  }
}


@include px1024 {
  .footer {
    padding: 32px 0px;

    &__inner {}

    &__logo {}

    &__links {
      gap: 40px;
    }

    &__link {
      font-size: 18px;
      line-height: 36px;
    }

    &__socials {
      gap: 12px;
    }
    &__social {
      transform: scale(0.9);
    }
  }
}


@include px768 {
  .footer {
    padding: 24px 0px;

    &__inner {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: min-content min-content;
      justify-content: space-between;
      align-items: center;
      row-gap: 24px;
    }

    &__logo {
      grid-column: 1/2;
    }

    &__logo > svg {
      display: none;
    }

    &__logo > div {
      display: block;
    }

    &__links {
      gap: 20px;      
      grid-column: 1/4;
      grid-row: 2/3;
      grid-template-columns: min-content min-content min-content;
      justify-content: center;
    }

    &__link {
      font-size: 14px;
      line-height: 24px;
    }

    &__socials {
      margin-right: -8px;
      grid-column: 3/4;
      gap: 0px;      
    }

    &__social {
      transform: scale(0.8);
      width: 48px;
      height: 48px;
    }
  }
}