.backed {
  position: relative;
  z-index: 10;
  background: var(--black);
  color: var(--white);
  padding-top: 120px;
  padding-bottom: 160px;

  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;    
  }

  &__title {
    font-weight: bold;
    font-size: 48px;
    line-height: 92px;
    text-align: center;
    margin-bottom: 44px;
    position: relative;

    &::before {
      content: url("../img/paints5.png");
      width: 100%;
      height: 100%;
      position: absolute;
      z-index: -1;
      top: 16px;
      left: -20px;
    }
  }

  &__rows {
    display: flex;
    flex-direction: column;
    width: 100%;
  }

  &__row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 36px;
    flex-wrap: wrap;
  }

  &__row-img-block {
    display: inline-block;
  }

  &__row-img {
    min-width: 100px;
  }

  &__row2 {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: min-content min-content min-content;
    gap: 28px;
    justify-content: center;
  }

  &__row2-person {
    white-space: nowrap;
    padding: 8px 24px;
    border: 2px solid rgba(255, 255, 255, 0.15);
    border-radius: 12px;
    font-weight: bold;
    font-size: 18px;
    line-height: 32px;
  }
}


@include px1024 {
  .backed {
    padding-top: 20px;
    padding-bottom: 80px;
    &__inner {}

    &__title {
      font-size: 40px;
      line-height: 52px;
      &::before {
        content: url("../img/paints5.png");
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        top: 16px;
        left: -35px;
        transform: scale(.9);
      }
    }

    &__rows {}

    &__row {
      margin-bottom: 28px;
    }

    &__row-img-block {}

    &__row-img {
      transform: scale(.65);
    }

    &__row-img-block > svg {
      transform: scale(.65);
    }

    &__row2 {
      gap: 20px;
    }

    &__row2-person {
      white-space: nowrap;
      padding: 8px 24px;
      border: 2px solid rgba(255, 255, 255, 0.15);
      border-radius: 12px;
      font-weight: bold;
      font-size: 14px;
      line-height: 24px;
    }
  }
}


@include px768 {
  .backed {
    padding-top: 0px;
    padding-bottom: 80px;
    &__inner {}

    &__title {
      font-size: 28px;
      line-height: 28px;
      margin-bottom: 32px;

      &::before {
        content: url("../img/paints5.png");
        width: 100%;
        height: 100%;
        position: absolute;
        z-index: -1;
        top: 10px; 
        left: -45px;
        transform: scale(.6);
      }
    }

    &__rows {}

    &__row {
      margin-bottom: 28px;
      justify-content: center;
    }

    &__row-img-block {}

    &__row-img {
      transform: scale(.65);
    }

    &__row-img-block>svg {
      transform: scale(.65);
    }

    &__row2 {
      display: grid;
      gap: 12px;
      grid-auto-flow: column;
      grid-template-columns: min-content min-content;    
      grid-template-rows: min-content min-content;
      justify-content: center;
      align-items: center;
    }

    &__row2-person {
      padding: 8px 20px;
      text-align: center;
    }
    &__row2-person:nth-child(2) {
      grid-column: 1/3;
      grid-row: 2/3;
    }
  }
}